

* { box-sizing: border-box; }

.ty-wrap {
  min-height: 100vh;
  padding: 40px 16px;
  background: black;
  display: grid;
  grid-template-columns: 1fr;
  gap: 24px;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: var(--ink);

 

}

@media (min-width: 900px) {
  .ty-wrap {
    grid-template-columns: 520px 1fr;
    align-items: start;
    padding: 60px 32px;
  }
}

.ty-card {

  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.12);
  backdrop-filter: blur(6px);
   background-color:rgb(51, 50, 50) ;
}

.ty-title {
  margin: 0 0 8px;
  font-size: 2rem;
  color: #eee;
}

.ty-sub {
  margin: 0 0 20px;
  color: whitesmoke
}

.ty-form {
  display: grid;
  gap: 12px;
}

.ty-input,
.ty-textarea {
  width: 100%;
  padding: 12px 14px;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size: 1rem;
  background: #fff;
}

.ty-textarea {
  resize: vertical;
  min-height: 120px;
}

.ty-btn {
  padding: 12px 20px;
  background: #102d6dff;
  color: #fff;
  font-weight: 700;
  border: none;
  border-radius: 999px;
  cursor: pointer;
  transition: background 0.25s ease;
}

.ty-btn:hover { background: var(--pink-dark); }

.ty-success {
  margin-top: 6px;
  font-weight: 600;
  color: #0f9d58;
}

.ty-list {
  background-color:rgb(51, 50, 50);
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.12);
}

.ty-list-title {
  margin: 0 0 16px;
  color: whitesmoke;
}

.ty-empty { color: var(--muted); }

.ty-notes {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  gap: 12px;
}

.ty-note {
  display: grid;
  grid-template-columns: 48px 1fr;
  gap: 12px;
  padding: 12px;
  border: 1px solid #1e1d1d;
  border-radius: 12px;
  background: #fff;
  color: black;
}

.ty-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: var(--pink);
  color: black;
  display: grid;
  place-items: center;
  font-weight: 800;
  letter-spacing: 0.5px;
}

.ty-note-head {
  display: flex;
  align-items: center;
  gap: 8px;
}

.ty-note-name {
  font-weight: 700;
}

.ty-note-text {
  margin: 4px 0 0;
  color: var(--ink);
  line-height: 1.5;
}
